import Vue from 'vue'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'

// 承载 (单一页面就用一个主页就行了)
// import verifyApplyInfo from '../pages/verifyApplyInfo.vue'
// 主页
import verifyAddress from '../views/verify-address.vue'

import 'html5-reset/assets/css/reset.css'

Vue.use(Router)


const router = new Router({
  routes: [{
      path: '/',
      name: 'verifyAddress',
      component: verifyAddress
    }
  ]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#verifyAddress',
  router,
  components: {
    verifyAddress
  },
  template: '<verifyAddress/>'
})



