<template>
  <div id="verifyAddress" style="width: 750px;margin: 0 auto;">
     <!-- <router-view style="width: 750px;margin: 0 auto;"/> -->
     <div class="container">
        <div class='hasData' v-if="datas.hasAddress">
            <p class="lable">收货人姓名</p>
            <p class='value'>{{datas.userName}}</p>

            <p class="lable">收货地址</p>
            <p class='value'> {{datas.address}}</p>

            <p class="lable">邮政编码</p>
            <p class='value'>{{datas.postCode}}</p>

            <p class="lable">手机号码</p>
            <p class='value'>{{ datas.tel | telFormat }}</p>

            <div class="editAddress" @click="editAddress">
                编辑收货地址
            </div>
        </div>
        <div class="null" v-else>
            <p>暂无收货地址信息</p>
            <p>只有填写完整的收货地址信息才能保证众测产品</p>
            <p>送到你的手中。快去填写吧~</p>
        </div>

    </div>
    
    <!-- 按钮start -->
    <div class="btn" @click="btn">{{datas.hasAddress?'确定':'填写收货地址'}}</div>
    <!-- 按钮end -->

    <div :class="this.isloading?'showLoad':'hideLoad'" class="shade"  >
      <img class="loading" src="../assets/svg/Spinner-1s-200px.svg">
    </div>

  </div>

</template>
<script>
export default {
  components: {},
  data() {
    return {
      isloading: false,
      datas: {
        hasAddress: true,
        userName: "傻啦啦啦",
        userId: 110,
        address: "广东省深圳市南山区蛇口网谷万海大厦 C#401",
        postCode: "518000",
        tel: 18649210300
      }
    };
  },
  filters: {
    telFormat: function(tel) {
      tel = tel.toString();
      return tel.replace(/\B(?=(\d{4})+(?!\d))/g, " ");
    }
  },
  methods: {
    editAddress() {},
    btn(e) {
      if (e.target.innerHTML == "确定") {
        // toast()
      } else {
        // writeAddress()
      }
    },
    // 接收data
    getData(data) {
      this.datas = data;
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@m: 750/375;
/* 主体区域 */

.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px * @m solid transparent;
  font-size: 14px * @m;
  height: 565px * @m;
  overflow: auto;
  margin-top: 30px * @m;
}

.hasData {
  display: block;
}

.null {
  //   display: none;
  margin-top: 132px * @m;
}

.null p {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 7px * @m;
}

.null p:nth-child(1) {
  font-size: 16px * @m;
  color: #435771;
}

.null p:nth-child(2) {
  font-size: 13px * @m;
  color: #c6c6c6;
}

.null p:nth-child(3) {
  font-size: 13px * @m;
  color: #c6c6c6;
}

.lable {
  color: #bcbcbc;
  margin-bottom: 10px * @m;
  font-size: 16px * @m;
}

.value {
  color: #0f2540;
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

/* 按钮 */

.editAddress {
  background: #e9eef4;
  border-radius: 100px * @m;
  width: 120px * @m;
  height: 40px * @m;
  line-height: 40px * @m;
  text-align: center;
  color: #a6b1bf;
  margin-top: 30px * @m;
  border: 1 solid #fff;
}

.btn {
  position: relative;
  left: 50%;
  bottom: 0;
  display: inline-block;
  transform: translateX(-50%);
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: auto;
  box-sizing: border-box;
  padding: 12px * @m 22px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 16px * @m;
  text-align: center;
  font-size: 16px * @m;
  margin-top: 10px * @m;
}
.hideLoad {
  display: none !important;
}
.showLoad {
  display: block !important;
}
.shade {
  position:fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 99;
}
.loading {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>



